<template>
    <div>
        <h1>고객관리</h1>
    </div>
    <div>
        <button><router-link to="/customerForm">고객등록</router-link></button>
    </div>
    <div>
        <span>id</span><span>name</span><span>email</span><span>phone</span><span>address</span>
    </div>
    <div v-for="customer in customers">
        <span>{{customer.id}}</span>
        <span @click="">{{customer.name}}</span>
        <span>{{customer.email}}</span>
        <span>{{customer.phone}}</span>
        <span>{{customer.address}}</span>
    </div>
</template>
<script>
    import axios from 'axios';

    export default {
    data(){
    return{
        customers : []
    };
    },
    created(){
        //프론트에서는 원래가지고있던 포트를 보여줘야하기때문에
        const url = '/api/customer'; //http://localhost:8080/customer
        axios.get(url)
        .then(json => {this.customers = json.data
        })
    },
    methods : {}
    }
</script>
<style>
    span{
        display: inline-block;
        width: 20%;
    }
</style>